import { publicApi } from "@/lib/http";

// call apis that do not require authentication

interface CreateUserPayload {
    name: string;
    email: string;
    password: string;
    role: string;
    status: "Active" | "Inactive";
}

interface UserResponse {
    id: string;
    name: string;
    email: string;
    role: string;
    status: "Active" | "Inactive";
}

export async function createUser(payload: CreateUserPayload): Promise<UserResponse> {
    try {
        const response = await publicApi.post<UserResponse>("/api/users", payload);
        return response;
    } catch (error: any) {
        throw new Error(error.response?.data?.message || "Failed to create user");
    }
}
