'use client';

import { useState } from 'react';
import { Button } from "@heroui/react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useUserStore } from "../../../stores/user/userStore";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { createUser } from "../../../features/auth/api";

// Validation Schema
const userSchema = Yup.object().shape({
    name: Yup.string()
        .min(2, 'Name must be at least 2 characters')
        .required('Name is required'),
    email: Yup.string()
        .email('Invalid email format')
        .required('Email is required'),
    password: Yup.string()
        .min(6, 'Password must be at least 6 characters')
        .required('Password is required'),
    role: Yup.string()
        .required('Role is required'),
    status: Yup.string()
        .required('Status is required')
});

export default function CreateUserPage() {
    const router = useRouter();
    const { addUser } = useUserStore();
    const [isLoading, setIsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState<string | null>(null);

    const initialValues = {
        name: '',
        email: '',
        role: 'Engineer',
        status: 'Active' as 'Active' | 'Inactive',
        password: ''
    };

    const handleSubmit = async (values: typeof initialValues, { resetForm }: any) => {
        try {
            setIsLoading(true);
            setErrorMessage(null);

            // Call API to create user
            const response = await createUser({
                name: values.name,
                email: values.email,
                password: values.password,
                role: values.role,
                status: values.status
            });

            // Add user to local store
            addUser({
                id: response.id,
                name: response.name,
                email: response.email,
                role: response.role,
                status: response.status
            });

            // Reset form and redirect
            resetForm();
            router.push('/user');
        } catch (error) {
            const errorMsg = error instanceof Error ? error.message : 'Failed to create user. Please try again.';
            setErrorMessage(errorMsg);
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Header */}
            <div className="flex items-center justify-between mb-8">
                <div>
                    <h1 className="text-3xl font-bold mb-2">Create New User</h1>
                    <p className="text-gray-400 text-sm">Add a new user to the system</p>
                </div>
                <Link href="/user">
                    <Button className="bg-zinc-800 hover:bg-zinc-700 border-zinc-700 text-white transition-colors">
                        ‚Üê Back to Users
                    </Button>
                </Link>
            </div>

            {/* Error Alert */}
            {errorMessage && (
                <div className="mb-6 p-4 bg-red-900/30 border border-red-700/50 rounded-lg">
                    <p className="text-red-300 text-sm font-medium">{errorMessage}</p>
                </div>
            )}

            {/* Form */}
            <div className="bg-zinc-900 rounded-lg border border-zinc-800 p-8 max-w-2xl shadow-xl">
                <Formik
                    initialValues={initialValues}
                    validationSchema={userSchema}
                    onSubmit={handleSubmit}
                >
                    {({ errors, touched, isSubmitting }) => (
                        <Form className="space-y-6">
                            {/* Full Name */}
                            <div>
                                <label htmlFor="name" className="text-sm text-gray-300 block mb-2 font-medium">
                                    Full Name <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    type="text"
                                    id="name"
                                    name="name"
                                    placeholder="Enter full name"
                                    disabled={isLoading}
                                    className={`w-full bg-black border rounded-lg px-4 py-2.5 text-sm text-white transition-colors focus:outline-none ${
                                        errors.name && touched.name
                                            ? 'border-red-500 focus:border-red-500'
                                            : 'border-zinc-800 focus:border-zinc-600'
                                    } disabled:opacity-50`}
                                />
                                {errors.name && touched.name && (
                                    <p className="text-red-400 text-xs mt-1">{errors.name}</p>
                                )}
                            </div>

                            {/* Email */}
                            <div>
                                <label htmlFor="email" className="text-sm text-gray-300 block mb-2 font-medium">
                                    Email Address <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    type="email"
                                    id="email"
                                    name="email"
                                    placeholder="user@oilgas.com"
                                    disabled={isLoading}
                                    className={`w-full bg-black border rounded-lg px-4 py-2.5 text-sm text-white transition-colors focus:outline-none ${
                                        errors.email && touched.email
                                            ? 'border-red-500 focus:border-red-500'
                                            : 'border-zinc-800 focus:border-zinc-600'
                                    } disabled:opacity-50`}
                                />
                                {errors.email && touched.email && (
                                    <p className="text-red-400 text-xs mt-1">{errors.email}</p>
                                )}
                            </div>

                            {/* Password */}
                            <div>
                                <label htmlFor="password" className="text-sm text-gray-300 block mb-2 font-medium">
                                    Password <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    type="password"
                                    id="password"
                                    name="password"
                                    placeholder="Enter password (min 6 characters)"
                                    disabled={isLoading}
                                    className={`w-full bg-black border rounded-lg px-4 py-2.5 text-sm text-white transition-colors focus:outline-none ${
                                        errors.password && touched.password
                                            ? 'border-red-500 focus:border-red-500'
                                            : 'border-zinc-800 focus:border-zinc-600'
                                    } disabled:opacity-50`}
                                />
                                {errors.password && touched.password && (
                                    <p className="text-red-400 text-xs mt-1">{errors.password}</p>
                                )}
                            </div>

                            {/* Role */}
                            <div>
                                <label htmlFor="role" className="text-sm text-gray-300 block mb-2 font-medium">
                                    Role <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    as="select"
                                    id="role"
                                    name="role"
                                    disabled={isLoading}
                                    className={`w-full bg-black border rounded-lg px-4 py-2.5 text-sm text-white transition-colors focus:outline-none cursor-pointer ${
                                        errors.role && touched.role
                                            ? 'border-red-500 focus:border-red-500'
                                            : 'border-zinc-800 focus:border-zinc-600'
                                    } disabled:opacity-50 disabled:cursor-not-allowed`}
                                >
                                    <option value="">-- Select a role --</option>
                                    <option value="Administrator">Administrator</option>
                                    <option value="Field Supervisor">Field Supervisor</option>
                                    <option value="Engineer">Engineer</option>
                                </Field>
                                {errors.role && touched.role && (
                                    <p className="text-red-400 text-xs mt-1">{errors.role}</p>
                                )}
                            </div>

                            {/* Status */}
                            <div>
                                <label htmlFor="status" className="text-sm text-gray-300 block mb-2 font-medium">
                                    Status <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    as="select"
                                    id="status"
                                    name="status"
                                    disabled={isLoading}
                                    className={`w-full bg-black border rounded-lg px-4 py-2.5 text-sm text-white transition-colors focus:outline-none cursor-pointer ${
                                        errors.status && touched.status
                                            ? 'border-red-500 focus:border-red-500'
                                            : 'border-zinc-800 focus:border-zinc-600'
                                    } disabled:opacity-50 disabled:cursor-not-allowed`}
                                >
                                    <option value="">-- Select status --</option>
                                    <option value="Active">Active</option>
                                    <option value="Inactive">Inactive</option>
                                </Field>
                                {errors.status && touched.status && (
                                    <p className="text-red-400 text-xs mt-1">{errors.status}</p>
                                )}
                            </div>

                            {/* Actions */}
                            <div className="flex gap-3 pt-6 border-t border-zinc-800">
                                <Button
                                    type="submit"
                                    disabled={isLoading || isSubmitting}
                                    className={`bg-red-600 hover:bg-red-700 text-white font-medium px-8 transition-colors ${
                                        isLoading || isSubmitting ? 'opacity-70 cursor-not-allowed' : ''
                                    }`}
                                >
                                    {isLoading ? 'Creating...' : 'Create User'}
                                </Button>
                                <Link href="/user">
                                    <Button
                                        type="button"
                                        disabled={isLoading}
                                        className={`bg-zinc-800 hover:bg-zinc-700 border-zinc-700 text-white transition-colors ${
                                            isLoading ? 'opacity-50 cursor-not-allowed' : ''
                                        }`}
                                    >
                                        Cancel
                                    </Button>
                                </Link>
                            </div>
                        </Form>
                    )}
                </Formik>
            </div>
        </div>
    );
}
